import "./style.css";
import { useState, useEffect } from "react";

const currentDate = (date) => {
    date.toLocaleDateString(undefined,
        {
            weakday: "long",
            day: "numeric",
            month: "long",
            year: "numeric",
            hour: "numeric",
            minute: "numeric",
            second: "numeric",
        }
    )
};


export const Clock = () => {
    const [date, setDate] = useState(new Date());
    useEffect(() => {
        let intervalID = setInterval(
            setDate(new Date())
            , 1000);

        return ()=>clearInterval(intervalID);
    }, []);

return (
    <div className="clock">
        Dzisiaj jest {currentDate(date)}
    </div>
)

}
